
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap');


.worksFull{
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: black;
    z-index: 1;
}


.worksHeader{
    display: flex;
    justify-content: center;
    font-size: 150px;
    color: rgba(#fff, 0.8);
    z-index: 9;
}

.particle{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
}


.firstWeb{    
    transition: .4s;
    width: 350px;
    height: 250px;
    margin-top: 5rem;
    background-image: url('../images/Screenshot_1\ \(1\).png');
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 2;
    display: flex;
    justify-content: center;
    text-align: center;
}

.firstWeb:hover{
    margin-top: 4.5rem;
    .webText{
        transition: .4s;
        background-color: rgba($color: #000000, $alpha: 0.9);
        color: rgba(#fff, 0.7);
        padding-top: 2rem;
    }
    box-shadow: 0 2px 12px rgba(255, 255, 255, .2);
    .subHeadingWeb{
        display: flex;
        position: absolute;
        padding-top: 5.5rem;
        color: #fff;
        font-size: 30px;
    }
}

.subHeadingWeb{
    display: none;
}

.worksSubHeading{
    font-size: 70px;
    position: absolute;
    z-index: 3;
    color: #fff;
    padding-top: 4rem;

}

.exitWeb{
    color: #fff;
}

.secondWeb{
    margin-left: 8rem;
    background-image: url('../images//Screenshot_2.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.webSitesBlock{
    display: flex;
    margin-left: 18rem;
    flex-direction: row;
}

.webText{
    transition: .4s;
    width: 100%;
    padding-top: 5.5rem;
    font-family: sans-serif;
    font-size: 35px;
    color: rgba($color: #fff, $alpha: 0.9);
    background-color: rgba($color: #000000, $alpha: 0.4);
}

.secondBlockTextWeb{
    color: rgba($color: #fff, $alpha: 0.9);
    color: #fff;
    padding-left: 0;
}

@media screen and(max-width: "700px"){ 
    .worksHeader{
        display: flex;
        justify-content: center;
        font-size: 60px;
        color: rgba(#fff, 0.8);
        z-index: 9;
    }
    
    .particle{
        display: none;
    }
    
    .firstWeb:hover{
        margin-top: 1rem;
        .webText{
            transition: .4s;
            background-color: rgba($color: #000000, $alpha: 0.9);
            color: rgba(#fff, 0.7);
            padding-top: 1rem;
        }
        box-shadow: 0 2px 12px rgba(255, 255, 255, .2);
        .subHeadingWeb{
            display: flex;
            position: absolute;
            padding-top: 4rem;
            color: #fff;
            font-size: 30px;
        }
    }
    .firstWeb{    
        transition: .4s;
        width: 200px;
        height: 150px;
        margin-top: 1rem;
        margin-left: 3rem;
        background-image: url('../images/Screenshot_1\ \(1\).png');
        background-repeat: no-repeat;
        background-size: cover;
        z-index: 2;
        display: flex;
        justify-content: center;
        text-align: center;
    }
   
    .subHeadingWeb{
        display: none;
    }
    
    .worksSubHeading{
        font-size: 30px;
        padding-left: 4rem;
        display: flex;
        justify-content: center;
        top: -30px;
    
    }
    .secondWeb{
        margin-left: 3rem;
        background-image: url('../images//Screenshot_2.png');
        background-repeat: no-repeat;
        background-size: cover;
    }
    
    .webSitesBlock{
        display: block;
        margin-left: 1rem;
        flex-direction: initial;
    }
    
    .webText{
        transition: .4s;
        width: 100%;
        padding-top: 3rem;
        font-family: sans-serif;
        font-size: 25px;
        color: rgba($color: #fff, $alpha: 0.9);
        background-color: rgba($color: #000000, $alpha: 0.4);
    }
    
    .secondBlockTextWeb{
        color: rgba($color: #fff, $alpha: 0.9);
        color: #fff;
        padding-left: 0;
    }
}