.fifthBlockMobile{
    transition: .4s;
    top: 0px;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: black;
    background-image: url(https://transcode4.app.engoo.com/image/fetch/f_auto,c_lfill,w_800,h_600,dpr_3/https://assets.app.engoo.com/images/hOmwte2PWgu9n3ltHmfBvkH5j7dNNwildDAOZY2Z8L3.jpeg);
    background-size: cover;
    filter: grayscale(0.8);
    background-repeat: no-repeat;    
}

.fifthBlockTextMobile{
    display: flex;
    justify-content: center;
    color: rgba(255,255,255, 0.45);
    padding-top: 1.5rem;
    font-size: 50px;
    word-break: keep-all;  
}

.underSub{
    font-size: 30px;
    top: -10px;
    padding-left: 5.9rem;
    position: absolute;
}

.contactsLinkMobile{
    transition: .4s;
    text-decoration: none;
    position: absolute;
    top: 40%;
    left: 15%;
    background-color: rgba($color: #fff, $alpha: 0.8);
    height:45px;
    width: 12rem;
    display: flex;
    justify-content: center;
    border: 3px solid rgba($color: #fff, $alpha: 0.9);
}

.secondMobile{
    margin-top: 7rem;
}