$white: #fff;
$black: #000;


.fullAbout{
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(https://upload.wikimedia.org/wikipedia/commons/d/d1/Mount_Everest_as_seen_from_Drukair2_PLW_edit.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
}

.header{
    padding-right: 1rem;
    font-size: 150px;
}

.aboutWord{
    color: rgba($color: $white, $alpha: 0.7);
}
.meWord{
    color: $white;
    letter-spacing: -37px;
}

.exit{
    color: $black;
    position: fixed;
    margin-top: 10px;
    margin-left: 10px;
    left: 0;
    z-index: 10;
}

.subHeader{
    position: absolute;
    padding-top: 20rem;
    display: inline-flex;
}

.myName{
    font-size: 70px;
    color: rgba($color: $white, $alpha: 0.9);
    padding-right: 10px;
}

.border{
    width: 2px;
    height: 120px;
    background-color: $white;
}
.webDev{
    padding-left: 10px;
    font-size: 40px;
    color: rgba($color: $black, $alpha: 0.8);
}


.about{
    position: absolute;
    padding-top: 45rem;
    display: inline-block;
    padding-bottom: 5rem;
}

.text{
    font-size: 10px;
    display: block;
}

.textBlock{
    font-size: 30px;
    padding-top: 3rem;
    padding-right: 1rem;
    float: left;
    left: 0;
    max-width: 40rem;
}

.myPhoto{
    right: 0;
    padding-top: 1rem;
    height: 45rem;
    width: 32rem;
    float: right;
    padding-left: 1rem;
    background-image: url(https://res.cloudinary.com/devgbpvtk/image/upload/v1592374298/samples/me_rl6rxi.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

@media screen and(max-width: "700px"){
    .header{
        font-size: 50px;
        .meWord{
            letter-spacing: -10px;
        }
    }
    .myName{
        padding-top: 20px;
        font-size: 20px;
    }
    .webDev{
        font-size: 20px;
    }
    .border{
        height: 70px;
    }
    .subHeader{
        padding-top: 10rem;
    }
    .myPhoto{
        display: none;
    }
    .about{
        padding-top: 29rem;
    }
    .text{
        font-size: 20px;
        padding-left: 1rem;
        padding-right: 1rem;
    }
}

@media screen and (min-width: "1500px"){
    .about{
        padding-top: 70rem;
    }
    .text{
        font-size: 40px;
        padding-left: 10rem;
        padding-right: 10rem;
    }
    .myPhoto{
        width: 40rem;
    }
}