@import url("https://fonts.googleapis.com/css?family=Raleway:400,400i,700");

/* MAIN DIV THAT CONTAINS EVERYTHING */

.fullMain{
    display: flex;
    position: absolute;
    flex-direction: row;
    width: 100%;
    height: 100%;
}

/* CREATING THE FIRST BLOCK WITH MY NAME */


.none{
    display: none;
}

.link {
    color: #FFF;
    transition: 4s;
    text-decoration: none;
}


.frame {
    transition: .7s;
	position: absolute;
	bottom: 50%;
	left: 35%;
	border-radius: 2px;
    box-shadow: 4px 8px 16px 0 rgba(0, 0, 0, 0.3);
    color: rgba(0,0,0, 0.9);
    font-size: 20px;
	overflow: hidden;
	font-family: Raleway, Helvetica, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	display: flex;
	justify-content: center;
    align-items: center;
}
.frame:hover{
    transition: 1s;
    color: rgba(255, 255, 255, 1);
}
.button {
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;

	svg {
		height: 55px;
		width: 150px;
		fill: none;
		stroke-width: 5;

		.o1 {
            stroke: rgba(255, 255, 255, 0.3);
			fill: rgba(255,255,255, 0.45);
			transition: all 1s ease-in-out;
		}

		.o2 {
			stroke: white;
			stroke-dasharray: 20 420;
			stroke-dashoffset: 20;
			transition: all 1s ease-in-out;
		}
	}

	span {
		position: absolute;
        margin: auto 0;
		text-transform: uppercase;
		letter-spacing: 1px;
	}

	&:hover {
		.o1{
            fill: rgba(0,0,0, 0.3);
		}
		
		.o2 {
			stroke-dashoffset: -420;
		}
	}
}


.overChange{
    position: absolute;
    display: flex;
    justify-content: center;
    z-index: -1;
    color: rgba(255,255,255, 0.45);
    width: 130%;
    left: -140px;
    font-size: 90px;
}

.firstBlock{
    top: 0px;
    width: 60%;
    background-color: black;
    background-image: url(https://www.cunard.com/content/dam/cunard/inventory-assets/ports/NYC/NYC.jpg);
    filter: grayscale(0.8);
    background-repeat: no-repeat;
    background-size: cover;
}

.firstBlockOverHeading{
    position: absolute;
    display: flex;
    justify-content: center;
    z-index: -1;
    color: rgba(255,255,255, 0.45);
    width: 130%;
    left: -140px;
    font-size: 130px;
}

.nameBlock{
    position: absolute;
    display: block;
    bottom: 0;
    padding-bottom: 17rem;
    padding-left: 4rem;
}

.firstBlockHeading{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: rgba(255, 255, 255, 0.95);
    font-size: 50px;
}
.firstBlockSubHeading{
    font-family: 'Raleway', sans-serif;
    color: rgba(255, 255, 255, 0.95);
    font-size: 30px;    
}

/* CREATING THE SECOND BLOCK */


.secondBlock{
    transition: .4s;
    top: 0px;
    width: 10%;
    height: 100%;
    background-color: black;
    background-image: url(https://www.mackinac.org/media/images/2019/photo-1486312338219-ce68d2c6f44d.jpeg);
    filter: grayscale(0.8);
    background-repeat: no-repeat;
    background-size: cover;
}

.secondBlockText{
    transition: .4s;
    padding-top: 10px;
    padding-left: 2.7rem;
    transition: .4s;
    font-family: sans-serif;
    color: rgba(255, 255, 255, 0.99);
    font-size: 80px;
    width: 1px;
    word-break: break-all;
}

.secondBlock:hover{
    .secondBlockText{
        color: rgba(255,255,255, 0.45);
        font-size: 120px;
        word-break: keep-all;
    }
    transition: 0.55s;
    width: 50%;
    display: flex;
    box-shadow: 0 0 150px  rgba(0,0,0, 1);
}

.secondUnderText{
    transition: .3s;
    padding-top: 3rem;
    padding-right: 30px;
    font-family: sans-serif;
    color: rgba(255, 255, 255, 0.99);
    font-size: 100px;
}

/* CREATING THE THIRD BLOCK */
.thirdBlock{
    transition: .4s;
    top: 0px;
    width: 10%;
    height: 100%;
    background-color: black;
    background-image: url(https://upload.wikimedia.org/wikipedia/commons/d/d1/Mount_Everest_as_seen_from_Drukair2_PLW_edit.jpg);
    filter: grayscale(0.8);
    background-repeat: no-repeat;
}

.thirdBlock:hover{
    .thirdBlockText{
        color: rgba(255,255,255, 0.45);
        font-size: 90px;
        word-break: keep-all;
    }
    .aboutMe{
        transition: .4s;
        top: 9px;
        margin-left: 21rem;
        font-size: 90px;
    }
    transition: .55s;
    width: 50%;
    display: flex;
    box-shadow: 0 0 150px  rgba(0,0,0, 1);
    @media screen and (min-width: 1520px) {
        .thirdBlockText{
            padding-left: 5rem;
            font-size: 100px;
        }
        .aboutMe{
            padding-left: 5.5rem;
            font-size: 100px;
        }
    }
}

.aboutMe{
    transition: .4s;
    bottom: 70px;
    position: absolute;
    font-family: sans-serif;
    color: rgba(255, 255, 255, 1);
    font-size: 80px;
    padding-left: 1rem;
    letter-spacing: -20px;
}
.thirdBlockText{
    padding-top: 10px;
    padding-left: 2.7rem;
    transition: .4s;
    font-family: sans-serif;
    color: rgba(255, 255, 255, 0.99);
    font-size: 80px;
    width: 1px;
    word-break: break-all;
}

/* CREATING THE FOURTH BLOCK */
.fourthBlock{
    transition: .4s;
    top: 0px;
    width: 10%;
    height: 100%;
    background-color: black;
    background-image: url(https://66.media.tumblr.com/79f919cedb105beac3fd61ab2e914709/tumblr_o4mead5YVL1uqyj3lo1_500.gif);
    filter: grayscale(0.8);
    background-repeat: no-repeat;
    background-size: cover;
}

.fourthBlock:hover{
    transition: .55s;
    width: 53%;
    display: flex;
    box-shadow: 0 0 150px  rgba(0,0,0, 1);
    .fourthBlockText{
        color: rgba(255,255,255, 0.45);
        font-size: 120px;
        word-break: keep-all;
    }
    @media screen and (min-width: 1520px) {
        .fourthBlockText{
            font-size: 100px;
        }
    }
}

.fourthBlockText{
    padding-top: 10px;
    padding-left: 2.7rem;
    transition: .4s;
    font-family: sans-serif;
    color: rgba(255, 255, 255, 0.99);
    font-size: 80px;
    width: 1px;
    word-break: break-all;   
}

/* CREATING THE FIFTH BLOCK */

.fifthBlock{
    transition: .4s;
    top: 0px;
    width: 10%;
    height: 100%;
    background-color: black;
    background-image: url(https://transcode4.app.engoo.com/image/fetch/f_auto,c_lfill,w_800,h_600,dpr_3/https://assets.app.engoo.com/images/hOmwte2PWgu9n3ltHmfBvkH5j7dNNwildDAOZY2Z8L3.jpeg);
    filter: grayscale(0.8);
    background-repeat: no-repeat;    
}

.fifthBlock:hover{
    transition: .55s;
    width: 70%;
    display: flex;
    box-shadow: 0 0 150px  rgba(0,0,0, 1);
    .fifthBlockText{
        color: rgba(255,255,255, 0.45);
        font-size: 100px;
        word-break: keep-all;       
    }
    @media screen and (min-width: 1520px) {
        .fifthBlockText{
            font-size: 100px;
        }
    }
}

.fifthBlockText{
    padding-top: 10px;
    padding-left: 2.7rem;
    transition: .4s;
    font-family: sans-serif;
    color: rgba(255, 255, 255, 0.99);
    width: 1px;
    word-break: break-all;  
    font-size: 60px;
}

.contactsLink{
    transition: .4s;
    text-decoration: none;
    position: absolute;
    top: 40%;
    left: 15%;
    background-color: rgba($color: #fff, $alpha: 0.8);
    height:45px;
    width: 12rem;
    display: flex;
    justify-content: center;
    border: 3px solid rgba($color: #fff, $alpha: 0.9);
}

.contactsLinkText{
    transition: .4s;
    color: rgba($color: #000000, $alpha: 0.8);
    position: absolute;
    top: 2px;
    right: 25px;
    font-size: 23px;
}
.contactsIcon{
    transition: .4s;
    position: absolute;
    width: 40px;
    height: 40px;
    padding-top: 2px;
    padding-left: 4px;
    padding-right: 4px;
    top: 4%;
    left: 0;
    color: #000;
    background-color: rgba($color: #000000, $alpha: 0.1);
}

.contactsLinkName{
    display: none;
}

.contactsLink:hover{
    transition: .4s;
    background-color: #fff;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
    .contactsIcon{
        transition: .4s;
        background-color: #fff;
    }
    .contactsLinkText{
        transition: .4s;
        top: 0;
        right: 5px;
        font-size: 10px;
    }
    .contactsLinkName{
        position: absolute;
        display: flex;
        font-size: 24px;
        bottom: 5px;
        font-family: sans-serif;
        left: 30%;
    }
}
.second{
    left: 55%;
}
.secondName{
    left: 30%;
}
.secondLink{
    right: 18%;
}

