@media screen and (min-width: 1520px) {
    .link{
        margin-left: 14rem;
        font-size: 50px;
    }
    .secondBlockText {
        font-size: 150px;
    }
    .thirdBlockText{
        font-size: 150px;  
    }
    .fourthBlockText{
        font-size: 150px;  
    }
    .fifthBlockText{
        font-size: 110px;  
    }
    .firstBlockOverHeading{
        font-size: 180px;
    }
    .nameBlock{
        padding-bottom: 40rem;
    }
    .aboutMe{
        font-size: 130px;
        letter-spacing: -30px;
    }
    .fifthText{
        font-size: 110px;
    }

    .contactsLink{
        left: 15%;
    }
    .second{
        left: 55%;
    }
    // .third{
    //     left: 62%;    
    // }
  }

@media screen and(max-width: "700px"){
    .fifthBlock{
        display: none;
    }
    .secondBlock{
        display: none;
    }
    .thirdBlock{
        display: none;
    }
    .fourthBlock{
        display: none;
    }
    .firstBlock{
        width: 100%;
    }
    .firstBlockOverHeading{
        padding-top: 5rem;
        font-size: 40px;
        left: 0px;
        width: 100%;
    }
    .nameBlock{
        position: absolute;
        display: block;
        bottom: 0;
        padding-bottom: 7rem;
        padding-left: 1rem;
    }
    .firstBlockHeading{
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        color: rgba(255, 255, 255, 0.95);
        font-size: 35px;
    }
    .firstBlockSubHeading{
        font-family: 'Raleway', sans-serif;
        color: rgba(255, 255, 255, 0.95);
        font-size: 25px;    
    }
}