@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap');
$rectengular: #e7dfd5;
$white: #fff;
$black: black;

.fullPage{
    display: block;
    width: 100%;
    height: 100%;
}

//CREATING THE HEADING

.heading{
    padding-left: 5rem;
    font-size: 130px;
    font-family: 'Roboto Condensed', sans-serif;
}
.subHeading{
    font-size: 50px;
    position: absolute;
    top: 5.8rem;
    padding-left: 5.8rem;
    font-family: serif;
    color: rgba($color: $rectengular, $alpha: 0.7);
}
.title{
    font-size: 40px;
    top: 9rem;
    position: absolute;
    font-family: sans-serif;
}

//SETING KNOWLEDGE ITEMS
.knowledgeItems{
    display: inline-block;
    position: absolute;
    margin-left: 30rem;
    margin-bottom: 4rem;
    top: 0;
    right: 0;
}


.triangular{
    transition: 1s;
    z-index: -2;
    position: absolute;
    top: 0;
    right: 0; 
    border-top:20.7rem solid transparent;
    border-bottom: 20.5rem solid transparent; 
    border-right:50rem solid $rectengular;     
}


@media screen and(min-width: "1540px"){
    .triangular{
        border-top:33.5rem solid transparent;
        border-bottom: 33.5rem solid transparent; 
        border-right:70rem solid $rectengular; 
    }
}

@media screen and(max-width: "700px"){
    .heading{
        font-size:70px;
        padding-left: 3.5rem;
    }
    .subHeading{
        font-size: 30px;
        top: 1.5rem;
        padding-left: 3rem;
    }
    .title{
        font-size: 17px;
        top: 5rem;
    }
    .knowledgeItems{
        top: 7rem;
        display: flex;
        justify-content: center;
        text-align: center;
        margin-left: 0;
        margin-bottom: 4rem;
    }
    .triangular{
        border-top:19rem solid transparent;
        border-bottom: 16rem solid transparent;
        border-right:30rem solid $rectengular;  
    }
}